<template>
  <div class="form-wrap">
    <div class="form-info">
      <h2>새글작성</h2>
      <!-- <a href="javascript:;" @click="returnGo"
        ><mdicon name="keyboard-return"
      /></a> -->
      <form @submit.prevent="submitForm">
        <div>
          <label for="title">제목</label>
          <input type="text" id="title" v-model="title" />
        </div>
        <div>
          <label for="contents">내용</label>
          <textarea type="text" id="contents" v-model="contents" rows="5" />
        </div>
        <button type="submit">등록하기</button>
      </form>
    </div>
  </div>
</template>

<script>
import { createPost } from "@/api/posts";
export default {
  data() {
    return {
      title: "",
      contents: "",
    };
  },
  methods: {
    async submitForm() {
      try {
        const data = {
          title: this.title,
          contents: this.contents,
        };
        const response = await createPost(data);
        console.log(response);
        this.$router.push("/main");
      } catch (error) {
        console.log(error);
      }
    },
    // returnGo() {
    //   console.log(this.$router.go);
    // },
  },
};
</script>

<style scoped lang="scss">
.form-wrap {
  display: flex;
  justify-content: center;

  .form-info {
    width: 100%;
    background: #fff;
    padding: 2%;
    border-radius: 10px;
    h2 {
      text-align: center;
    }
    a {
      color: #000;
    }
    button {
      margin: 30px 0 10px;
      width: 100%;
      padding: 10px;
      transition: all 0.1s ease-in-out;
      background: #ff8f00;
      color: #fff;
      &:hover {
        background: #ffd54f;
      }
    }
    input {
      margin: 5px;
    }
  }
}
</style>
